<template>
    <div
        id="app"
        class="flex items-center justify-center"
    >
        <div class="relative flex-col-reverse text-lg">
        <button
            class="flex items-center justify-between px-3 py-3 text-bas w-fit font-medium rounded-full "
            :class="$store.state.appMode == 'ans' ? 'bg-gray-900  text-white' : $store.state.appMode == 'sorry' ? 'bg-ballon-dark text-white' : 'bg-coach-dark text-white'"
            @click="isOptionsExpanded = !isOptionsExpanded"
            @blur="isOptionsExpanded = false"
        >
            <svg 
                xmlns="http://www.w3.org/2000/svg" 
                fill="none" viewBox="0 0 24 24"
                stroke-width="1.5" stroke="currentColor"
                class="w-6 h-6 transform transition-transform duration-200 ease-in-out"
                :class="isOptionsExpanded ? 'rotate-180' : 'rotate-0'"
            >
                <path 
                stroke-linecap="round" 
                stroke-linejoin="round" 
                d="M3.75 6.75h16.5M3.75 12h16.5m-16.5 5.25h16.5"
                />
            </svg>
            <span class="pl-2 hidden sm:block">{{ selectedOption }}</span>
        </button>
        <transition
            enter-active-class="transform transition duration-500 ease-custom"
            enter-class="translate-y-1/2 scale-y-0 opacity-0"
            enter-to-class="translate-y-0 scale-y-100 opacity-100"
            leave-active-class="transform transition duration-300 ease-custom"
            leave-class="translate-y-0 scale-y-100 opacity-100"
            leave-to-class="translate-y-1/2 scale-y-0 opacity-0"
        >
            <ul
            v-show="isOptionsExpanded"
            class=" w-24 absolute left-0 bottom-0 right-0 mb-14 bg-white divide-y rounded-lg shadow-lg overflow-hidden"
            >
                <li
                    class="px-3 py-2 transition-colors duration-300 "
                    :class="$store.state.appMode == 'cheer' ? ' bg-coach-dark text-coach-tlight' : ($store.state.appMode == 'sorry' ? 'bg-green-100 text-gray-600 hover:bg-ballon-light hover:text-gray-900 hover:cursor-pointer' : ' bg-gray-100 text-gray-700 hover:bg-gray-400 hover:text-gray-900 hover:cursor-pointer')"
                    @mousedown.prevent="setOption('تشجيع')"
                    >
                    شجعني
                </li>
                <li 
                    class="px-3 py-2 transition-colors duration-300 "
                    :class="$store.state.appMode == 'sorry' ? 'bg-ballon-dark text-white' : ($store.state.appMode == 'ans' ? 'bg-gray-100 text-gray-700 hover:bg-gray-400 hover:text-gray-900 hover:cursor-pointer' : ' bg-red-100 text-gray-700 hover:bg-coach hover:text-coach-tlight hover:cursor-pointer')"
                    @mousedown.prevent="setOption('معلشة')"
                    >
                    معلشني
                </li>
                <li
                    class="px-3 py-2 transition-colors duration-300  "
                    :class="$store.state.appMode == 'ans' ? 'bg-gray-900 text-white' : ($store.state.appMode == 'sorry' ? 'bg-green-100 text-gray-600 hover:bg-ballon-light hover:text-gray-900 hover:cursor-pointer' : ' bg-red-100 text-gray-700 hover:bg-coach hover:text-coach-tlight hover:cursor-pointer')"
                    @mousedown.prevent="setOption('اسئلني')"
                    >
                    جاوبني
                </li>
            </ul>
        </transition>
        </div>
    </div>
</template>

<script>
  
    import { useStore } from 'vuex'

    export default {
        data() {
        return {
            isOptionsExpanded: false,
            selectedOption: "mode",
        };
        },
        methods: {
        setOption(option) {
            if(option == "اسئلني") {
                this.checkAns();
                this.selectedOption = "mode";
                this.isOptionsExpanded = false;
            } else if(option == "معلشة") {
                this.checkSorry();
                this.selectedOption = option;
                this.isOptionsExpanded = false;
            } else if(option == "تشجيع") {
                this.checkCheer();
                this.selectedOption = option;
                this.isOptionsExpanded = false;
            }
        }
        },
        setup() {
        const store = useStore()


        const checkAns = () => {
            store.dispatch('checking', "ans")
        }
        const checkSorry = () => {
            store.dispatch('checking', "sorry")
        }
        const checkCheer  = () => {
            store.dispatch('checking', "cheer")
        }
        
        return { checkCheer, checkSorry, checkAns }
    },

    };
</script>
  
  <style>
  .ease-custom {
    transition-timing-function: cubic-bezier(.61,-0.53,.43,1.43);
  }
  </style>
  