<template>
      <div v-for="chat in allChats" 
        :key="chat.msg"
        class=""
        >
        <div
            class="flex items-end pt-1 pb-1"
            :class="chat.id == 'hum' ? 'justify-end' : ''"
            >
            <img
                v-if="chat.id == 'hum' ? false : true"
                :src="$store.state.appMode == 'ans' ? require('./../assets/img/bobert2x200.png') : $store.state.appMode == 'sorry' ? require('./../assets/img/ballonx200.png') : require('./../assets/img/coachx200.png')"
                :class="$store.state.appMode == 'ans' ? 'h-10 sm:h-12 md:h-14' : $store.state.appMode == 'sorry' ? 'h-12 sm:h-14 md:h-16' : 'h-10 sm:h-12 md:h-14'"
                >
            <div 
                class="flex flex-col space-y-2 max-w-xs text-xs md:text-base md:max-w-lg mx-2 order-1"
                :class="chat.id == 'hum' ? 'items-end' : 'items-start'"
                >
                <div>
                    <div 
                        :style= "$store.state.lang == 'en' ? 'text-align: left;':'text-align: right;'"
                        class="w-full font-medium px-4 py-2 rounded-2xl inline-block whitespace-pre-wrap"
                        :class="chat.id == 'hum' ? ( $store.state.appMode == 'ans' ? 'bg-gray-800 text-gray-100 rounded-br-none' : $store.state.appMode == 'sorry' ? 'bg-gray-800 text-white rounded-br-none' : 'bg-gray-800 text-white   rounded-br-none') :  ($store.state.appMode == 'ans' ? ' bg-slate-300 text-gray-900   rounded-bl-none' : $store.state.appMode == 'sorry' ? 'text-gray-800 bg-green-300 rounded-bl-none' : 'text-gray-800 bg-red-300   rounded-bl-none')"
                        >
                        {{ chat.msg }}
                    </div>
                </div>
            </div>

        </div>
      </div>
</template>

<script>
import { computed, onUpdated } from 'vue'
import { useStore } from 'vuex'

export default {
    name: "chat",
    setup() {
        const store = useStore();
        const allChats = computed (() => store.state.chats);

        const scrolling = () => {
            store.dispatch('scrollEnd')
        }

        onUpdated(() => {
			scrolling();
		});
        
        return { allChats, scrolling }
       
    },
    // computed: {
    //     allChats() {
    //         return this.$store.state.chats;
    //     }
    // },
}
</script>
