<template>
    <div class=" relative flex flex-col">
        <span class="absolute inset-y-0 flex items-center">
            <SendDropDown  />
            </span>
        <input
            dir="auto"
            type="text"
            v-model="message"
            placeholder="Ask . . ."
            class=" w-full focus:outline-none focus:placeholder-gray-400 text-gray-600 placeholder-gray-600 bg-gray-300 pl-16 sm:pl-28 pr-16 rounded-r-2xl rounded-l-3xl py-3"
            @keydown.enter.prevent="sending"
        />
        <div class="absolute right-0 items-center inset-y-0 sm:flex">
            <button
                @click.prevent="sending"
                type="button"
                class="inline-flex items-center justify-center rounded-full px-3 py-3 transition duration-500 ease-in-out bg-none focus:outline-none"
                :class="$store.state.appMode == 'ans' ? 'text-gray-900  hover:text-gray-700' : $store.state.appMode == 'sorry' ? 'text-ballon-dark  hover:text-ballon' : 'text-coach-dark  hover:text-coach'"
                >
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill="currentColor" class="h-7 w-7 ml-2 transform rotate-90">
                    <path d="M10.894 2.553a1 1 0 00-1.788 0l-7 14a1 1 0 001.169 1.409l5-1.429A1 1 0 009 15.571V11a1 1 0 112 0v4.571a1 1 0 00.725.962l5 1.428a1 1 0 001.17-1.408l-7-14z"></path>
                </svg>
            </button>
        </div>
    </div>
</template>

<script>
import { ref } from 'vue'
import { useStore } from 'vuex'
import SendDropDown from './sendDropDown.vue'

export default {
    name: 'send',
    components: {
        SendDropDown
    },
    setup() {
        const store = useStore()
        const message = ref('')

        const sending = () => {
            
            store.dispatch('sendMessage', message)
        }
        
        return { sending, message }
    },

}
</script>


