  <div class="bg-backG-light  flex-1 p:2 md:px-36 justify-between flex flex-col h-screen">
    <div class=" z-30 flex py-3 md:py-5 border-b-2 border-backG">
      <div class=" ml-5 md:ml-10">
        <h1
          class=" text-center text-3xl font-bold "
          :class="$store.state.appMode == 'ans' ? 'text-gray-900' : $store.state.appMode == 'sorry' ? 'text-ballon-dark' : 'text-coach-dark'"
          >
          Chat MGPT4
        </h1>
      </div>
      <div class=" mr-6 md:mr-48 absolute right-0">
        <svg
          @click="showInfo"
          xmlns="http://www.w3.org/2000/svg"
          fill="none" viewBox="0 0 24 24"
          stroke-width="1.5" stroke="currentColor"
          class="w-8 md-12 hover:cursor-pointer"
          :class="$store.state.appMode == 'ans' ? 'text-gray-900  hover:text-gray-700' : $store.state.appMode == 'sorry' ? 'text-ballon-dark  hover:text-ballon' : 'text-coach-dark  hover:text-coach'"
          >
          <path stroke-linecap="round" stroke-linejoin="round" d="M11.25 11.25l.041-.02a.75.75 0 011.063.852l-.708 2.836a.75.75 0 001.063.853l.041-.021M21 12a9 9 0 11-18 0 9 9 0 0118 0zm-9-3.75h.008v.008H12V8.25z" />
        </svg>
      </div>
    </div>
    <div id="scroller" class=" flex flex-col space-y-4 p-3 overflow-y-auto scrollbar-thumb-blue scrollbar-thumb-rounded scrollbar-track-blue-lighter scrollbar-w-2 scrolling-touch">
        <chat />
    </div>
    <div v-if="$store.state.gotYou" class="sticky bottom-0 z-30 bg-backG-light px-4 pt-4 mb-2 sm:mb-0 sm:pb-4 pb-2">
      <send />
    </div>
  </div>
</template>

<script>
import chat from './chat.vue';
import popup from './popup.vue';
import send from './send.vue';

import { ref } from 'vue'
import { useStore } from 'vuex'

// import { mapGetters } from 'vuex';

  export default {
    name: 'appChatBot',
    components: {
        chat,
        popup,
        send,
    },
    setup() {
        const store = useStore()

        const Info = ref('')

        const showInfo = () => {
            store.dispatch('showInfo', true)
        }
        return { showInfo, Info }
    },
  }
</script>


<style>
.scrollbar-w-2::-webkit-scrollbar {
  width: 0.25rem;
  height: 0.25rem;
}

.scrollbar-track-blue-lighter::-webkit-scrollbar-track {
  --bg-opacity: 1;
  background-color: #E9E9F2;
  background-color: rgba(233, 233, 242, var(--bg-opacity));
}

.scrollbar-thumb-blue::-webkit-scrollbar-thumb {
  --bg-opacity: 1;
  background-color: #D0D0D9;
  background-color: rgba(208, 208, 217, var(--bg-opacity));
}

.scrollbar-thumb-rounded::-webkit-scrollbar-thumb {
  border-radius: 0.25rem;
}
</style>
